import React, { useCallback } from 'react';
import { useTranslation } from 'react-i18next';
import {
  Box, Text, HStack, Flex, useClipboard
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { format } from 'date-fns';
import Icon from '../../../../components/Icon';
import style from './AccountDetails.module.css';
import { useAppSelector } from '../../../../hooks/store';
import { currencyFormat, getDisclosures } from '../../../../utils/general';
import MiniBox from '../../../../layout/MiniBox';
import SkeletonLoader from '../../../../components/SkeletonLoader';
import { useDrawer } from '../../../../hooks/drawer';

const AccountStatement = React.lazy(() => import('../AccountStatement/AccountStatement'));

const AccountDetails = () => {
  const { t } = useTranslation();
  const { close, open } = useDrawer();

  const details = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const { hasCopied: hasAccountCopied, onCopy: onAccountCopy } = useClipboard(details?.accountNumber || '', 2000);
  const { hasCopied: hasRoutingCopied, onCopy: onRoutingCopy } = useClipboard(details?.routingNumber || '', 2000);

  const accountStatement = useCallback(() => {
    if (details) { open(AccountStatement, { id: details?.id }); }
  }, [details]);

  return (
    <div className={`${style.pageBg} page`}>
      {/* (Update loader) */}
      {!details ? (
        <SkeletonLoader name="AccountDetails" />
      ) : (
        <MiniBox
          showBack
          onBack={() => {
            close();
          }}
          heading={t('account_details.title')}
        >
          <Box width="100%" paddingTop="15px">
            <Text variant="large">{details.label}</Text>
            <Box className={style.balanceBox} border="1px solid var(--border-color-1)">
              <HStack justify="space-between" pb="32px">
                <Text variant="regular" className={`${style.balanceTitle} text-light`} textAlign="left">{t('account_details.balance')}</Text>
                <Text variant="regular" className={style.balanceAmount} textAlign="right">
                  {currencyFormat(details.availableBalance, details.currency)}
                </Text>
              </HStack>
              <Flex justifyContent="space-between">
                <Box>
                  <Text variant="regular" className="text-light" textAlign="left">{t('account_details.accountNumber')}</Text>
                  <Flex alignItems="center">
                    <Text variant="regular" className={style.numberValue} textAlign="left">{details.accountNumber}</Text>
                    {hasAccountCopied
                      ? <Icon icon="tick" cursor="pointer" className="icon-customizable" size={12} />
                      : <Icon onClick={onAccountCopy} icon="copy" cursor="pointer" className="icon-customizable" size={12} /> }
                  </Flex>
                </Box>
                <Box>
                  <Text variant="regular" className="text-light" textAlign="left">{t('account_details.routingNumber')}</Text>
                  <Flex alignItems="center">
                    <Text variant="regular" className={style.numberValue} textAlign="left">{details.routingNumber}</Text>
                    {hasRoutingCopied
                      ? <Icon icon="tick" cursor="pointer" className="icon-customizable" size={12} />
                      : <Icon onClick={onRoutingCopy} icon="copy" cursor="pointer" className="icon-customizable" size={12} /> }
                  </Flex>
                </Box>
              </Flex>
            </Box>

            <Text variant="large">{t('account_details.otherDetails')}</Text>
            <Box className={style.otherDetails}>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" className={`${style.otherDetailsInfoLabel} text-light`} textAlign="left">{t('account_details.type')}</Text>
                <Text variant="regular" className={style.otherDetailsInfoValue} textAlign="left">{t(`account_details.accountType${details.type}`)}</Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" className={`${style.otherDetailsInfoLabel} text-light`} textAlign="left">{t('account_details.dateOpened')}</Text>
                <Text variant="regular" className={style.otherDetailsInfoValue} textAlign="left">{format(new Date(details.createdAt), "MMM d, yyyy 'at' h:mm aa")}</Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" className={`${style.otherDetailsInfoLabel} text-light`} textAlign="left">{t('account_details.sponsorBank')}</Text>
                <Text variant="regular" className={style.otherDetailsInfoValue} textAlign="left">{details.sponsorBankName}</Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" className={`${style.otherDetailsInfoLabel} text-light`} textAlign="left">{t('account_details.interest')}</Text>
                <Text variant="regular" className={style.otherDetailsInfoValue} textAlign="left">
                  {details.interest}
                  %
                  {' '}
                  APY
                </Text>
              </Box>
              <Box className={style.otherDetailsInfo}>
                <Text variant="regular" className={`${style.otherDetailsInfoLabel} text-light`} textAlign="left">{t('account_details.fees')}</Text>
                <Text variant="regular" className={style.otherDetailsInfoValue} textAlign="left">
                  {currencyFormat(details.fees)}
                  /
                  {details.accountInterestFrequency}
                </Text>
              </Box>
            </Box>

            <Text variant="large" mb="16px">{t('account_details.actions')}</Text>
            <Box role="link" onClick={accountStatement}>
              <HStack className={style.statement}>
                <Text variant="regular" textAlign="left">{t('account_details.statements')}</Text>
                <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
              </HStack>
              {/* <HStack className={style.statement}>
          <Text>{t('account_details.lockWithdrawals')}</Text>
          <Icon icon="Arrow-small-right" size={12} />
        </HStack>
        <HStack className={style.statement}>
          <Text>{t('account_details.limits')}</Text>
          <Icon icon="Arrow-small-right" size={12} />
        </HStack> */}
              <Box
                role="button"
                onClick={() => {
                  getDisclosures();
                }}
              >
                <HStack className={style.statement}>
                  <Text variant="regular" textAlign="left">{t('account_details.disclosures')}</Text>
                  <Icon icon="Arrow-small-right" size={12} className="svg-light-grey-1" />
                </HStack>
              </Box>
            </Box>
          </Box>
        </MiniBox>
      )}

    </div>

  );
};

export default AccountDetails;
